<template>
  <v-container
    id="dashboard"
    fluid
    tag="section"
    class="fondo"
  >
    <base-alert />
    <v-img
      :src="require('@/assets/fondoBienvenido3.png')"
    />
    <v-row>
      <v-col cols="12">
        <v-card elevation="7">
          <v-card-title class="primary white--text">
            Datos básicos
          </v-card-title>
          <v-card-text>
            <v-row>
              <v-col
                lg="4"
                md="4"
                cols="12"
              >
                <v-text-field
                  v-model="ficha.establecimientoSalud"
                  disabled
                  label="Establecimiento de salud"
                  outlined
                />
              </v-col>
              <v-col
                lg="4"
                md="4"
                cols="12"
              >
                <v-text-field
                  v-model="ficha.nombreYapellido"
                  disabled
                  label="Nombres y apellidos"
                  outlined
                />
              </v-col>
              <v-col
                lg="4"
                md="4"
                cols="12"
              >
                <p class="display-1 gray-text">
                  Sexo
                </p>
                <v-radio-group
                  v-model="ficha.sexo"
                  row
                  disabled
                >
                  <v-radio
                    label="Masculino"
                    value="masculino"
                  />
                  <v-radio
                    label="Femenino"
                    value="femenino"
                  />
                </v-radio-group>
              </v-col>
              <v-col
                lg="4"
                md="4"
                cols="12"
              >
                <v-text-field
                  v-model="ficha.numeroHoja"
                  disabled
                  label="Número de hoja"
                  outlined
                />
              </v-col>
              <v-col
                lg="4"
                md="4"
                cols="12"
              >
                <v-text-field
                  v-model="ficha.numeroHistoria"
                  disabled
                  label="Número de historia clinica"
                  outlined
                />
              </v-col>
            </v-row>
          </v-card-text>
        </v-card>
      </v-col>
      <v-col cols="12">
        <v-card elevation="7">
          <v-card-title class="primary white--text">
            <p>Administración</p>
          </v-card-title>
          <v-card-text class="mt-4">
            <tr
              v-for="(item, k) in ficha.administracionMedicamentos"
              :key="k"
            >
              <td>
                <v-text-field
                  v-model="item.nombre"
                  small
                  class="ml-2"
                  label="Nombre"
                  disabled
                  outlined
                />
              </td>
              <td>
                <v-text-field
                  v-model="item.hora"
                  small
                  class="ml-2"
                  label="Hora"
                  outlined
                  disabled
                />
              </td>
              <td>
                <v-text-field
                  v-model="item.fecha"
                  small
                  class="ml-2"
                  label="Fecha"
                  outlined
                  disabled
                />
              </td>
              <td>
                <v-select
                  v-model="item.funcion"
                  class="ml-2"
                  small
                  label="Función"
                  :items="funcion"
                  outlined
                  disabled
                />
              </td>
            </tr>
          </v-card-text>
        </v-card>
      </v-col>
      <v-col
        cols="12"
      >
        <v-card elevation="7">
          <v-card-title class="primary white--text">
            <p>Medicamentos</p>
          </v-card-title>
          <v-card-text class="mt-4">
            <tr
              v-for="(item, k) in ficha.medicamentos"
              :key="k"
            >
              <td>
                <v-text-field
                  v-model="item.nombre"
                  small
                  class="ml-2"
                  label="Nombre"
                  outlined
                  disabled
                />
              </td>
              <td>
                <v-text-field
                  v-model="item.presentacion"
                  class="ml-2"
                  small
                  label="Presentación"
                  outlined
                  disabled
                />
              </td>
              <td>
                <v-select
                  v-model="item.tipoVia"
                  class="ml-2"
                  small
                  label="Tipo vía"
                  :items="itemsPresentacion"
                  item-text="name"
                  item-value="value"
                  outlined
                  disabled
                  @change="cargarVia(k, item)"
                />
                <v-select
                  v-model="item.via"
                  class="ml-2"
                  small
                  label="Vía"
                  item-text="name"
                  item-value="value"
                  :items="cargarVia(k, item)"
                  outlined
                  disabled
                />
              </td>
              <td>
                <v-select
                  v-model="item.frecuencia"
                  class="ml-2"
                  small
                  label="Frecuencia"
                  :items="itemsFrecuencia"
                  outlined
                  disabled
                />
              </td>
              <td>
                <v-text-field
                  v-model="item.dosis"
                  class="ml-2"
                  small
                  placeholder="Ingrese la dosis"
                  outlined
                  disabled
                />
              </td>
            </tr>
          </v-card-text>
          <v-card-text>
            <v-textarea
              v-model="ficha.observaciones"
              outlined
              name="input-7-4"
              label="Observaciones"
              disabled
            />
          </v-card-text>
        </v-card>
      </v-col>
      <v-col
        cols="12"
      >
        <v-card elevation="7">
          <v-card-title class="primary white--text">
            <p>Plan de egreso</p>
          </v-card-title>
          <v-card-text>
            <v-row>
              <v-col
                lg="4"
                md="4"
                cols="12"
              >
                <v-text-field
                  v-model="ficha.nombreYapellido"
                  label="Nombres y apellidos"
                  outlined
                  disabled
                />
              </v-col>
              <v-col
                lg="4"
                md="4"
                cols="12"
              >
                <v-text-field
                  v-model="ficha.numeroAfiliacion"
                  label="Número afiliación"
                  outlined
                  disabled
                />
              </v-col>
              <v-col
                lg="4"
                md="4"
                cols="12"
              >
                <v-text-field
                  v-model="ficha.numeroHc"
                  label="Número HC"
                  outlined
                  disabled
                />
              </v-col>
              <v-col
                lg="4"
                md="4"
                cols="12"
              >
                <v-text-field
                  v-model="ficha.nombreServicio"
                  label="Nombre del servicio"
                  outlined
                  disabled
                />
              </v-col>
              <v-col
                lg="4"
                md="4"
                cols="12"
              >
                <v-text-field
                  v-model="ficha.fechaSalida"
                  label="Fecha de egreso"
                  outlined
                  disabled
                />
              </v-col>
              <v-col
                lg="4"
                md="4"
                cols="12"
              >
                <p class="display-1 gray-text">
                  Egreso en:
                </p>
                <v-radio-group
                  v-model="ficha.egresoEn"
                  row
                  disabled
                >
                  <v-radio
                    label="Silla de ruedas"
                    value="sillaRuedas"
                  />
                  <v-radio
                    label="Camilla"
                    value="camilla"
                  />
                  <v-radio
                    label="Caminando"
                    value="caminando"
                  />
                </v-radio-group>
              </v-col>
              <v-col
                lg="4"
                md="4"
                cols="12"
              >
                <v-textarea
                  v-model="ficha.diagnosticoEgreso"
                  disabled
                  outlined
                  name="input-7-4"
                  label="Diagnóstico del egreso"
                />
              </v-col>
            </v-row>
          </v-card-text>
        </v-card>
      </v-col>
      <v-col
        cols="12"
      >
        <v-card elevation="7">
          <v-card-title class="primary white--text">
            <p>Instrucciones para el paciente y su familia</p>
          </v-card-title>
          <v-card-text class="mt-4">
            <v-alert
              border="top"
              colored-border
              type="info"
              elevation="2"
            >
              La continuidad de su recuperación dependerá de su colaboración con las siguientes recomendaciones.
            </v-alert>
          </v-card-text>
          <v-card-subtitle class="mt-4">
            Medicación a suministrar:
          </v-card-subtitle>
          <v-card-text class="mt-4">
            <tr
              v-for="(item, k) in ficha.medicacionSumunistrada"
              :key="k"
            >
              <td>
                <v-text-field
                  v-model="item.nombre"
                  small
                  class="ml-2"
                  label="Medicamento"
                  outlined
                  disabled
                />
              </td>
              <td>
                <v-text-field
                  v-model="item.dosis"
                  class="ml-2"
                  small
                  label="Dosis"
                  outlined
                  disabled
                />
              </td>
              <td>
                <v-text-field
                  v-model="item.hora"
                  class="ml-2"
                  small
                  label="Hora"
                  outlined
                  disabled
                />
              </td>
            </tr>
          </v-card-text>
          <v-card-text>
            <v-row>
              <v-col
                lg="4"
                md="4"
                cols="12"
              >
                <v-textarea
                  v-model="ficha.dietaPermitida"
                  outlined
                  name="input-7-4"
                  label="Dieta permitida"
                  disabled
                />
              </v-col>
              <v-col
                lg="4"
                md="4"
                cols="12"
              >
                <v-textarea
                  v-model="ficha.dietaNoPermitida"
                  outlined
                  name="input-7-4"
                  label="Dieta no permitida"
                  disabled
                />
              </v-col>
              <v-col
                lg="4"
                md="4"
                cols="12"
              >
                <v-textarea
                  v-model="ficha.actividades"
                  outlined
                  name="input-7-4"
                  label="Actividades"
                  disabled
                />
              </v-col>
              <v-col
                lg="4"
                md="4"
                cols="12"
              >
                <v-textarea
                  v-model="ficha.educacionEspecial"
                  outlined
                  name="input-7-4"
                  label="Educación especial"
                  disabled
                />
              </v-col>
            </v-row>
          </v-card-text>
        </v-card>
      </v-col>
      <v-col
        cols="12"
      >
        <v-card elevation="7">
          <v-card-title class="primary white--text">
            <p>Proxima cita</p>
          </v-card-title>
          <v-card-text>
            <v-row>
              <v-col
                lg="6"
                md="6"
                cols="12"
              >
                <v-text-field
                  v-model="ficha.fechaCita"
                  class="ml-2"
                  small
                  label="Fecha"
                  outlined
                  disabled
                />
              </v-col>
              <v-col
                lg="6"
                md="6"
                cols="12"
              >
                <v-text-field
                  v-model="ficha.horaCita"
                  class="ml-2"
                  small
                  label="Hora"
                  outlined
                  disabled
                />
              </v-col>
              <v-col
                lg="6"
                md="6"
                cols="12"
              >
                <v-text-field
                  v-model="ficha.nombreMedico"
                  label="Nombre del medico"
                  outlined
                  disabled
                />
              </v-col>
              <v-col
                lg="6"
                md="6"
                cols="12"
              >
                <v-text-field
                  v-model="ficha.nombreEnfermera"
                  label="Nombre del enfermero/a responsable"
                  outlined
                  disabled
                />
              </v-col>
            </v-row>
          </v-card-text>
        </v-card>
      </v-col>
      <v-col
        cols="12"
      >
        <v-card elevation="7">
          <v-card-title class="primary white--text">
            <p>Redirigir a la ficha familiar</p>
          </v-card-title>
          <v-card-actions>
            <v-btn
              color="primary"
              center
              href="https://lit-everglades-34819.herokuapp.com/"
              target="_blank"
            >
              <v-icon right>
                mdi-open-in-new
              </v-icon>
              Ir a la ficha familar
            </v-btn>
          </v-card-actions>
        </v-card>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
  import { mapState } from 'vuex'
  export default {
    name: 'DashboardDashboard',
    data () {
      return {
        nombre: '',
        dateCita: '',
        timeCita: '',
        sexoGroup: null,
        egresoGroup: null,
        medicamento: '',
        medicamentosAgregados: [],
        itemsPresentacion: [
          {
            name: 'Vía enteral',
            value: 'viaEnteral',
            elementos: [
              { name: 'Vía oral', value: 'viaOral' },
              { name: 'Vía sublingual', value: 'viaSublingual' },
              { name: 'Vía rectal', value: 'viaRectal' },
            ],
          },
          {
            name: 'Via parenteral',
            value: 'viaParenteral',
            elementos: [
              { name: 'Intradérmica', value: 'intradermica' },
              { name: 'Subcutánea', value: 'subcutanea' },
              { name: 'Intramucular', value: 'intramucular' },
              { name: 'Endovenosa', value: 'endovenosa' },
              { name: 'Intracardíaca', value: 'intracardiaca' },
              { name: 'Intraraquídea', value: 'intraraquidea' },
              { name: 'Intraarticular', value: 'intraarticular' },
            ],
          },
        ],
        funcion: ['Doctor', 'Pediatra', 'Enfermería', 'Especialista'],
        itemsFrecuencia: ['Cada día', 'Cada hora', 'Cada dos horas', 'Cada tres horas', 'Cada cuatro horas', 'Cada cinco horas', 'Cada seis horas', 'Cada siete horas', 'Cada ocho horas', 'Cada nueve horas', 'Cada diez horas', 'Cada once horas', 'Cada doce horas'],
        medicamentos: [{
          nombre: '',
          presentacion: '',
          tipoVia: '',
          via: '',
          vias: [],
          frecuencia: '',
          dosis: '',
        }],
        medicamentosRecuperacion: [{
          nombre: '',
          dosis: '',
          hora: '',
          menu2Hora: false,
        }],
        medicamentosAdministrados: [{
          nombre: '',
          hora: '',
          menu2Hora: false,
          menu2: false,
          fecha: new Date().toISOString().substr(0, 10),
          funcion: '',
        }],
        dateEgreso: new Date().toISOString().substr(0, 10),
        dateFechaCita: new Date().toISOString().substr(0, 10),
        menu2FechaCita: false,
        menu2Hora: false,
        menu2Ingreso: false,
        time: null,
        modal2: false,
        loader: null,
        loading: false,
        loading2: false,
      }
    },
    computed: {
      ...mapState(['ficha']),
    },
    methods: {
      cargarVia (k, item) {
        // var idx = this.medicamentos.indexOf(item)
        var f = this.itemsPresentacion.find(res => {
          return res.value === item.tipoVia
        })
        var d = { ...f }
        return d.elementos
      },
      agregarMedicamento () {
        this.medicamentosAgregados.push(this.medicamento)
      },
      agregarFila () {
        this.medicamentos.push({
          nombre: '',
          presentacion: '',
          tipoVia: '',
          via: '',
          vias: [],
          frecuencia: '',
          dosis: '',
        })
      },
      eliminarFila (index, medicamento) {
        var idx = this.medicamentos.indexOf(medicamento)
        if (idx > -1) {
          this.medicamentos.splice(idx, 1)
        }
      },
      guardarMedicamento () {
        console.log(JSON.stringify(this.medicamentos))
      },
      agregarFilaEgresado () {
        this.medicamentosRecuperacion.push({
          nombre: '',
          dosis: '',
          hora: '',
        })
      },
      agregarFilaAdministracion () {
        this.medicamentosAdministrados.push({
          nombre: '',
          hora: '',
          menu2Hora: false,
          menu2: false,
          fecha: new Date().toISOString().substr(0, 10),
          funcion: '',
        })
      },
      eliminarFilaAdministracion (index, medicamento) {
        var idx = this.medicamentosAdministrados.indexOf(medicamento)
        if (idx > -1) {
          this.medicamentosAdministrados.splice(idx, 1)
        }
      },
      eliminarFilaEgresado (index, medicamento) {
        var idx = this.medicamentosRecuperacion.indexOf(medicamento)
        if (idx > -1) {
          this.medicamentosRecuperacion.splice(idx, 1)
        }
      },
    },
  }
</script>
